@import "../../global.scss";

.portfolioList {
    font-size: 34px;
    margin-right: 5px;
    padding: 15px;
    border-radius: 15px;
    cursor: pointer;
    transition: all .3s ease;
    @include mobile{
        max-width: 200px;
        font-size: 110%;
        overflow: hidden;
    }
    &.active{
        background-color: royalblue;
        color: black;
    }
}